import * as React from 'react';

import { IManifestStoreEntry } from './../../../../../ManifestStore';

import styles from './ManifestDetails.module.scss';

export interface IManifestDetailsProps {
  manifest: IManifestStoreEntry;
}

export default class ManifestDetails extends React.Component<IManifestDetailsProps, void> {
  public render(): React.ReactElement<IManifestDetailsProps> {
    const manifestText: string = JSON.stringify(this.props.manifest.manifest, undefined, 2);

    return  <div className={ styles.container }>
              <div className={ styles.manifestIdHeading }>
                {this.props.manifest.id}
              </div>
              <textarea className={styles.manifestTextBox}
                        readOnly='true'
                        value={manifestText} />
            </div>;
  }
}
